*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.main {
  background-color: black;
  height: 650px;
  padding: 20px;
}

.main div {
  margin-top: 350px;
  margin-left: 450px;
  color: white;
  line-height: 50px;
  letter-spacing: 20px;
  word-spacing: 50px;
  font-size: 25px;
  /* text-shadow: 1px 1px #e2e3e993; */
  /* animation: flip 2s infinite;
    animation-delay: calc(.2s * var(--i)); */
}
/* 
@keyframes flip {
    0%,80% {
      transform: rotateY(360deg) 
    }
  } */

.main #hello {
  letter-spacing: 30px;
  margin-left: 200px;
  margin-top: 100px;
  position: absolute;
  font-size: 100px;
  -webkit-writing-mode: vertical-rl;
          writing-mode: vertical-rl;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  -webkit-transform: scale(-1);
          transform: scale(-1);
  -webkit-animation: mymove 10s infinite;
          animation: mymove 10s infinite;
}

@-webkit-keyframes mymove {
  0% {
    left: 0px;
  }
  50% {
    left: 150px;
  }
  100% {
    left: 0px;
  }
}

@keyframes mymove {
  0% {
    left: 0px;
  }
  50% {
    left: 150px;
  }
  100% {
    left: 0px;
  }
}

.main span::before {
  letter-spacing: 5px;
  content: "Competitive Programmer";
  -webkit-animation: animate infinite 5s;
          animation: animate infinite 5s;
  word-spacing: 10px;
  font-size: 13px;
}

@-webkit-keyframes animate {
  0% {
    content: "Competitive Programmer";
  }

  33% {
    content: "Web Developer";
  }

  66% {
    content: "Final Year Student";
  }
}

@keyframes animate {
  0% {
    content: "Competitive Programmer";
  }

  33% {
    content: "Web Developer";
  }

  66% {
    content: "Final Year Student";
  }
}

.contact{
  padding : 40px;
  background-color: #FFF8CD;
}

.contact .center {
    margin: auto;
    width: 50%;
    /* border: 3px solid green; */
    padding: 10px;
  }
  
.contact  h1{
    text-align: center;
    font-size: 40px;
    text-decoration: underline;
  }
  
  .contact  form{
    display: grid;
    flex-flow: row wrap;
    align-items: center;
    /* flex-direction: row; */
  }
  .contact input ,  .contact textarea {
    border-radius: 5px;
    vertical-align: middle;
    margin: 5px 10px 5px 0;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd
  }
  
  .contact button{
    border-radius: 5px;
    padding: 10px 20px;
    background-color: rgb(69, 141, 212);
    border: 1px solid #ddd;
    color: white;
    /* max-width: 200%; */
    margin: auto;
    cursor: pointer;
    
  }
  .contact button:hover{
    background-color: #D61C4E;
}
  
